










































import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class WriteCell extends Vue {
  @Prop(String)
  private cell!: string;

  @Prop({ default: "" })
  private type!: string;

  get overlayGlyph() {
    if (
      this.cell.length >= 2 &&
      (this.cell.charCodeAt(0) < 32 || this.cell.charCodeAt(0) >= 256)
    ) {
      return this.cell[1];
    }
    return "";
  }

  get cellGlyph() {
    if (this.overlayGlyph) {
      return this.cell[0];
    }
    return this.cell;
  }

  get leftHalf() {
    if ([".", ","].includes(this.cell.trim())) {
      return true;
    }

    if (['" ', "' "].includes(this.cell)) {
      return true;
    }

    return false;
  }

  get rightHalf() {
    if ([' "', " '"].includes(this.cell)) {
      return true;
    }
    if (['"', "'"].includes(this.cell)) {
      return true;
    }

    return false;
  }
}
